
.view-id-galerias_home  {
  margin-bottom: 50px;
  ul {
    list-style: none;
    padding: 0px;
    @supports (display: grid) {
      .wrapper {
        display: grid;
        // grid-gap: 5px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: auto auto auto;
        justify-content: center;
        /* layout 1  */
        .box.a {
          grid-column: 1/5;
          grid-row:    1;
        }
        .box.b {
          grid-column: 1/3;
          grid-row:    2;
        }
        .box.c {

          grid-column: 3/5;
          grid-row:    2;
        }
        .box.d {
          grid-column: 1/5;
          grid-row:    3;
        }

        /* layout 2  */
        .box-1.a {
          grid-column: 1/3;
          grid-row:    1;
        }
        .box-1.b {
          grid-row:    1;
          grid-column: 3/5;
        }
        .box-1.c {
          grid-column: 1/5;
          grid-row:    2;
        }

        /* layout 3  */
        .box-2.a {
          grid-column: 1/4;
          grid-row:    1/5;
        }

        .box-2.b {
          grid-column: 4/5;
          grid-row:    1;
        }
        .box-2.c {
          grid-column: 4/5;
          grid-row:    2;
        }
        .box-2.d {
          grid-column: 4/5;
          grid-row:    3;
        }
        .box-2.e {
          grid-column: 4/5;
          grid-row:    4;
        }
      }
    }
